import {useState} from 'react'

export default function Menu ({open, children}){
    const [openMenu, setOpenMenu] = useState(open);
    /*const componentWillReceiveProps = open => {
        open !== openMenu ? setOpenMenu(open) : null
    }*/
    const handleClick = () => {
        setOpenMenu(openMenu)
    }
    return (
        <div>
            <div className="menu_list">
            {
                handleClick ? <div className="list">{children}</div> : ''
            }
            </div>
            <style jsx>
            {`
                .menu_list{
                    position: absolute;
                    top: 0;
                    left: 0;
                    height: ${openMenu ? '100%' : 0};
                    width: 100px;
                    display: flex;
                    flex-direction: column;
                    background-color: white;
                    opacity: 0.95;
                    color: black;
                    transition: height 0.3s ease;
                    z-index: 2;
                    }
                .list {
                    padding-top: 3rem;
                }
                `}
            </style>
        </div>
    )
}